import * as PIXI from "pixi.js";

/**
 * 
 * ```ts
 * class ExampleScene extends Scene {
 *   hoge: PIXI.Sprite;
 *   fuga: PIXI.Sprite;
 *   constructor() {
 *     super();
 *     // 
 *     this.hoge = this.spawn(new Sprite())
 *     // 
 *     this.fuga = new Sprite()
 *   }
 *   async main() {
 *     new Flow(this, (start, $) =>
 *       start
 *         // 1 
 *         .then(Flow.time(1))
 *         // 
 *         .then(() => $.spawn($.fuga))
 *         // 
 *         .then(Flow.loop((head) =>
 *           head
 *             // 0.1 
 *             .then(Flow.time(0.1, () => {
 *               $.fuga.angle++;
 *             })
 *           )
 *         ))
 *     );
 *   }
 * }
 * ```
 */
export class Scene extends PIXI.Container {
  isReady: boolean;
  isPlaying: boolean;
  constructor() {
    super();
    this.isReady = this.isPlaying = false;
  }
  /** main()  */
  ready() {
    this.isReady = this.isPlaying = true;
    this.main().finally(() => {
      this.isPlaying = false;
    });
  }
  /**
   * 
   * -  `Flow.loop`  `while(1)` 
   */
  async main() {
    // 
  }
  /**
   * 
   * ```ts
   * class ExampleScene extends Scene {
   *   // 1. 
   *   : PIXI.Container;
   *   : PIXI.Container;
   *   constructor() {
   *     super();
   *     // 2. 
   *     this. = this.spawn(new PIXI.Container());
   *     this.ready();
   *   }
   *   async main() {
   *     // 3. 
   *     this.spawn(this.);
   *   }
   * }
   * ```
   */
  spawn<T extends PIXI.Container>(object: T) {
    this.addChild(object);
    return object;
  }
}
